<template>
  <modal :modal-class="'register'" v-bind:class="{profile: isLoggedIn}" :on-close="close">
    <h3 slot="header" v-translate>{{headerTitle}}</h3>
    <div class="form" v-if="!isLoggedIn">
      <form v-on:submit.prevent id="form-register" name="form-register">
          <ul v-if="errors.errors.length" class="form_errors">
            <li v-for="error in errors.errors">{{ error.msg }}</li>
          </ul>
          <label v-translate>Email</label>
          <input type="email" name="username" v-model="username"
                 v-validate.initial="username" data-rules="required|email" :class="{'input': true, 'has-error': errors.has('username') }" required>
          <div class="clearfix cols">
            <div class="col-1">
              <label v-translate>fieldPassword</label>
              <input type="password" name="password" v-model="password"
                     v-validate.initial="password" data-rules="required|min:5" :class="{'input': true, 'has-error': errors.has('password') }" required>
            </div>
            <div class="col-2">
              <label v-translate>Confirm Password</label>
              <input type="password" name="password_cinfirm" v-model="password_cinfirm"
                     v-validate.initial="password_cinfirm" data-rules="required|confirmed:password" :class="{'input': true, 'has-error': errors.has('password_cinfirm') }" required>
            </div>
          </div>

          <div class="clearfix cols">
            <div class="col-1">
              <label v-translate>First Name</label>
              <input type="text" id="firstName" name="firstName" v-model="firstName"
                     v-validate.initial="firstName" data-rules="required" :class="{'has-error': errors.has('firstName') }" required>
            </div>
            <div class="col-2">
              <label v-translate>Last Name</label>
              <input type="text" id="lastName" name="login" v-model="lastName"
                     v-validate.initial="lastName" data-rules="required" :class="{'has-error': errors.has('lastName') }" required>
            </div>
          </div>

          <div class="clearfix">
            <div class="col-1">
              <label class="label-inline">
                <input type="radio" name="maleGender" value="true" v-model="maleGender"
                     v-validate.initial="maleGender" data-rules="required" :class="{'has-error': errors.has('maleGender') }">
                <span v-translate>Male</span>
              </label>
              <label class="label-inline">
                <input type="radio" name="maleGender" value="false" v-model="maleGender"
                     v-validate.initial="maleGender" data-rules="required" :class="{'has-error': errors.has('maleGender') }">
                <span v-translate>Female</span>
              </label>
            </div>
          </div>

          <div class="clearfix cols">
            <div class="col-1">
              <label v-translate>Phone</label>
              <input type="text" id="phone" name="phone" v-model="phone"
                     v-validate.initial="phone" data-rules="required|numeric" :class="{'has-error': errors.has('phone') }" required>
            </div>
            <div class="col-2">
              <label v-translate>Birthday</label>
              <input type="text" id="birthday" name="birthday" placeholder="YYYY/DD/MM" v-model="birthday"
                     v-validate.initial="birthday" data-rules="required" :class="{'has-error': errors.has('birthday') }" required>
            </div>
          </div>
          <button v-on:click.stop.prevent="submitAction" type="submit" v-bind:disabled="errors.errors.length > 0" class="button-primary">Register</button>
        </form>
      </div>
      <div v-if="isLoggedIn">
        <user-profile></user-profile>
      </div>
  </modal>
</template>


<style>
  @import "variables.css";

  .register .modal-container {
    max-width: 45rem;
    min-height: 23rem;
    height: auto;
  }

  .register  input[type="radio"] {
    width: auto;
    height:auto;
    margin-left: 2px;
  }


  button.register {
    background: none;
    color:#1eaedb;
    text-decoration: underline;
    display: inline-table;
  }

  button.register:hover {
    text-decoration: none;
  }

  .sign-in {
    display: block;
    text-align: center;
    padding:1rem 1rem 0;
    color: #2d67bb;
    font-size: 1.3rem;
  }

</style>


<script>
  import Modal from './Modal'
  import UserProfile from './UserProfile'
  import router from '../router/index'
  import notification from '../helpers/Notification'

  export default {
    data: function() {
      return {
        firstName: '',
        lastName: '',
        username: '',
        password: '',
        password_cinfirm: '',
        maleGender: '',
        phone: '',
        birthday: ''
      }
    },
    components: {
      Modal,
      UserProfile
    },
    computed: {
      isLoggedIn () {
        return !!this.$store.state.user
      },
      headerTitle () {
        return this.isLoggedIn ? 'Profile' : 'Register'
      }
    },
    methods: {
      close: function(){
        router.push({name:'date', params: this.$route.params})
      },
      submitAction: function(e) {
        this.$validator.validateAll();

        if (this.errors.any()) {
          e.preventDefault();
          return;
        }

        if(this.isLoggedIn){
          this.$store.dispatch('logoutUser');
          notification.info(this.t('Success, you are rigetered!'));
          return;
        }

        //if everything is OK
        this.$store.dispatch('registerUserAndReturnPromise', {
          username: this.username,
          password: this.password,
          enabled: true,
          firstName: this.firstName,
          lastName: this.lastName,
          maleGender: this.maleGender,
          phone: this.phone,
          birthday: new Date(this.birthday).getTime()
        }).then(() => {
          notification.success(this.t('Success, you are rigetered!'));
        }).catch((e)=>{
          notification.error(e.message || 'Undefined error on server.');
          this.password = '';
        });

      }
    }
  }
</script>



